<script>
  import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
  import "../node_modules/bootstrap/dist/css/bootstrap-grid.min.css";
  
  let groupsJsonStr = localStorage.getItem("itemGroups")
  let groups = localStorage.getItem("itemGroups");
  let editingGroup = -1;
  if (groupsJsonStr)
    groups = JSON.parse(groupsJsonStr)
  else
    groups = []

  let totalAmt = 0;

  $ : {
    totalAmt = 0;
    if (groups){
      localStorage.setItem("itemGroups", JSON.stringify(groups));
      // console.log(JSON.stringify(groups, null, 2));
      
      for(let items of groups.map((g)=>g.items)){
        
        let subtot = items.map((item) => item.price * item.qtysold).reduce((x, y) => x + y, 0);
        totalAmt += subtot ? subtot : 0;
        // console.log(item.price * item.qtysold)
      }
      
    }
    // console.log(groups.map((g)=>g.items).map((item=>item.price * item.qtysold)))
  }
  ;
  // $: totalAmt = 0

</script>

<!-- <svelte:head>
  <link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/bootstrap@5.1.0/dist/css/bootstrap.min.css">
</svelte:head> -->

<div class="container">
  <!-- <div class="table-responsive"> -->
  <div>
    <table class="table table-bordered table-hover align-middle">
      <thead style="font-size:larger;">
        <tr class="table-warning">
          <th class="text-center" style="width: 28%">ÂìÅÈ†Ö</th>
          <th style="width: 12%">ÂñÆÂÉπ</th>
          <th class="text-center">Êï∏Èáè</th>
          <th style="width: 20%">Â∞èË®à</th>
        </tr>
      </thead>
      {#each groups as group, i}
        <tbody>
          <tr class="table-info">
            <th colspan="100%">
              <span class="h4">
                {#if editingGroup == i}    
                  <div class="row">
                    <div class="col-5">
                      <div class="input-group input-group-lg">
                        <input type="text" class="form-control" placeholder="Item group name"  bind:value="{group.name}">
                      </div>
                      
                    </div>
                    <div class="col-auto">
                      <button class="btn btn-warning" on:click="{() => {editingGroup = -1;}}">Done</button>
                    </div>
                    <div class="col text-end">
                      <button class="btn btn-danger btn-sm" on:click="{() => {
                        groups = [...groups.slice(0, i), ...groups.slice(i + 1)];
                        editingGroup = -1;
                      }}">ÁßªÈô§</button>
                    </div>
                  </div>         
                  
                  <!-- <input class="p-1 form-control" type="text" size={(group.name && group.name.length) ? group.name.length: 1}  bind:value="{group.name}"> -->
                {:else}
                  {i+1}. {group.name && group.name.trim() !== '' ? group.name : "<no-name>"}
                {/if}
                
              </span>               
              
              {#if editingGroup == -1}
                <button class="btn btn-outline-secondary" on:click="{() => {editingGroup = i;}}">‰øÆÊîπ</button>
              {/if}

            </th>
          </tr>
          {#each group.items as item, j}
            <tr>
              <td>
                <div class="align-items-center">
                  
                  {#if editingGroup == i}
                    <div class="input-group align-items-center text-nowrap">
                      <input type="text" class="form-control text-center" style="font-size:large;" bind:value="{item.name}">
                      <button class="btn btn-outline-danger" on:click="{()=>{
                        group.items = [...group.items.slice(0, j), ...group.items.slice(j + 1)]
                      }}">üóë</button>
                    </div>
                  {:else}
                    <div class="text-center">
                      {item.name}
                    </div>
                  {/if}
                  
                </div>
                
              </td>
              <td class="text-nowrap">
                {#if editingGroup == i}
                  <div class="input-group">
                    <input type="number" class="form-control" aria-label="Amount" min="1" max="99999" bind:value="{item.price}">
                  </div>
                {:else}
                  $ {item.price}
                {/if}
              </td>
              <td >

                <div class="input-group text-center align-items-center text-nowrap">
                  <button on:click="{() => {if (item.qtysold > 0) item.qtysold -= 1;}}" class="btn btn-danger"> - </button>
                  {#if editingGroup == i}
                    <input class="text-center form-control" type="number" min="0" max="99999" bind:value="{item.qtysold}">
                  {:else}
                    <span class="form-control" style="border:none">{item.qtysold}</span> 
                  {/if}
                  <button on:click="{() => {item.qtysold += 1;}}" class="btn btn-success"> + </button>
                </div>
              </td>
              <td class="text-nowrap">
                $ {!item.price || !item.qtysold ? 0 : item.price * item.qtysold}
              </td>
            </tr>
          {/each}
          <tr style="font-weight:bold; font-size:large">
            <td colspan="3"></td>
            <td>
              $ {group.items.map((item) => item.price * item.qtysold).reduce((x,y) => {return x+y;}, 0)}
            </td>
          </tr>
          {#if editingGroup == i}
            <tr>
              <td colspan="100%">
                <div class="d-grid gap-2 col-5 mx-aut">
                  <button class="btn btn-outline-primary " type="button" on:click="{()=>{
                    group.items = [...group.items, {
                      "name": "",
                      "price": 0,
                      "qtysold": 0,
                    }]
                  }}">+ÂìÅÈ†Ö</button>
                </div>
              </td>
            </tr>
          {/if}
        </tbody>
      {/each}
      <tfoot>
        <tr style="font-weight:bold; font-size:x-large">
          <td colspan="3" style="text-align: right">
            Á∏ΩÈ°ç
          </td>
          <td>
            $ {totalAmt}
          </td>
        </tr>
      </tfoot>
    </table>
  </div>

  {#if editingGroup === -1}
    <button class="btn btn-primary btn-lg" on:click="{() => {
      groups = [...groups, {
          "name": "", 
          "items": [
            {
              "name": "",
              "price": 0,
              "qtysold": 0,
            }    
          ]
        }
      ]; 
      editingGroup = groups.length - 1
    }}">+ ÂïÜÂìÅÁæ§</button>
  {/if}

</div>

<style>
  button {
      touch-action: manipulation;
  }
  thead, thead tr, thead th{
    position: sticky; top: 0; z-index: 10;
  }

  td {
    font-size:large;
  }
